html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

.intro {
  margin: 20px;
}

p strong {
  color: #f9fafb;
}

button a {
  color: #f9fafb;
}

button.button {
  font-family: 'Raleway', sans-serif;
  background-color: #f78255;
  width: 180px;
  margin-bottom: 5px;
  /* font-weight: 300; */
  /* border: none; */
}

div p {
  font-family: 'Raleway', sans-serif;
  margin-bottom: 12px;
  color: #f9fafb;
}

div.spinnerDiv {
  width: '100%';
  height: '100%';
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  display: block;
}

.hide {
  display: none;
}

.show {
  display: block;
}



